.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 85vh;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  text-align: center;
}
h2 {
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.icon{
  margin: 15px
}
label {
  font-size: 1.2rem;
  margin-top: 1rem;
}
.back-button {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 700;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  color: #fff;
  background-color: gray;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 1rem 1rem 1rem 1rem
}

.back-button:hover {
  background-color: darkgray;

}
.scale{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin:15px
}

input[type="text"],
input[type="number"],
input[type="time"],
select,
input[type="range"] {
  font-size: 1.2rem;
  padding: 0.5rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-top: 0.5rem;
}

select {
  appearance: none;
  background-color: #fff;
  background-position: right 0.7rem top 50%;
  background-repeat: no-repeat;
  background-size: 0.65rem;
  padding-right: 1.5rem;
}

input[type="range"] {
  -webkit-appearance: none;
  background-color: #ddd;
  height: 0.5rem;
  border-radius: 1rem;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 1.5rem;
  height: 1.5rem;
  background-color: #007f7f;
  border-radius: 50%;
  cursor: pointer;
}


input[type="submit"] {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 700;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  color: #fff;
  background-color: #007bff;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 1rem 1rem 1rem 1rem

}

input[type="submit"]:hover {
  background-color: #0062cc;
}
.error-message {
  color: red;
  font-size: 16px;
  margin-top: 5px;
}
.flex{
  display: flex;
  flex-direction: column;
}
.alert-error{
  border-radius: 4px;
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  background-color: rgb(253, 237, 237);
  display: flex;
  padding: 8px 16px;
  flex-direction: row;
  width:50vw;
  align-items: center;
  margin-right: 16px;
}
.alert-title{
  color: red;
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-size: 1.5rem;
  line-height: 1.5;
  letter-spacing: 0.00938em;
  font-weight: 500;
}
.alert-description{
  color: darkred;
  font-size: 14px;
  padding: 4px 12px;
}
.error-icon{
  width: 3em;
  height: 3em;
  filter: invert(27%) sepia(68%) saturate(5733%) hue-rotate(351deg) brightness(96%) contrast(135%);
  margin:5px;
}
.sent{
  color: #198754
}
